import { mapState, mapMutations } from 'vuex';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
import articleList from '@/components/articleList.vue';
import uParse from '@/components/u-parse/u-parse.vue';
export default {
	data() {
		return {
			timer: null,
			videoContext: null,
			statusBarHeight: 0,
			height: 0,
			id: -1,
			show: false,
			article: null,
			author: null,
			is_collect: 0,
			is_like: 0,
			attention: 0,
			more: false,
			type: 0,
			list: [],
			comment: {
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			reply: {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			},
			showInput: false,
			showInput1: false,
			content: '',
			content1: '',
			item: null,
			to_user_name: ''
		};
	},
	computed: {
		...mapState(['isLogin', 'userInfo'])
	},
	components: { articleList, uniPopup, uParse },
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		this.$Fetch({
			url: 'news/share',
			data: { news_id: this.id },
			success: res => {
				let str = res > 0 ? '已分享,砖瓦+' + res : '已分享';
				uni.showToast({
					title: str,
					icon: 'none'
				});
			}
		});
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/article/article?id=' + this.id + '&server=' + user.server_people_id + '&path=/pages/index/article/article'
			};
		}
	},
	onLoad(opt) {
		let token = uni.getStorageSync('TOKEN');
		let that = this;
		if (!token) {
			this.setShareMsg({
				server: opt.server,
				path: opt.path + '?id=' + opt.id
			});
			uni.redirectTo({
				url: '/pages/login/login'
			});
			return false;
		}
		uni.getSystemInfo({
			success: function(res) {
				that.height = res.windowHeight / 2 + 'px';
				that.statusBarHeight = res.statusBarHeight;
			}
		});
		uni.hideKeyboard();
		if (opt.id) {
			this.timer = setTimeout(() => {
				this.$Fetch({
					url: 'news/read_news',
					data: { id: opt.id },
					success: res => {
						if (res > 0) {
							uni.showToast({
								title: '砖瓦数+' + res,
								icon: 'success'
							});
						}
					}
				});
			}, 5000);
			this.id = opt.id;
			this.$Fetch({
				url: 'news/getDataById',
				data: { id: opt.id },
				success: res => {
					this.article = res;
					this.is_collect = res.is_collect;
					this.is_like = res.is_like;
					this.author = res.adminUser;
					this.attention = res.attention;
					this.type = res.type;
					this.getRecommend();
				}
			});
			this.getCommentList();
		}
	},
	onHide() {
		this.videoContext.pause();
		console.log('页面隐藏了');
		clearTimeout(this.timer);
		this.timer = null;
	},
	onUnload() {
		console.log('页面销毁了');
		this.videoContext.pause();
		clearTimeout(this.timer);
		this.timer = null;
	},
	onReady() {
		this.videoContext = uni.createVideoContext('myVideo');
		setTimeout(() => {
			this.videoContext.play();
		}, 3000);
	},
	methods: {
		...mapMutations(['setShareMsg']),
		videoErrorCallback() {},
		back() {
			uni.navigateBack({});
		},
		doShare() {
			let that = this;
			uni.share({
				provider: 'weixin',
				scene: 'WXSceneSession', //WXSceneSession”分享到聊天界面，“WXSenceTimeline”分享到朋友圈，“WXSceneFavorite”分享到微信收藏
				type: 5, //0-图文；5-小程序
				imageUrl: '../../static/poster.png',
				title: '欢迎使用易建联APP',
				miniProgram: {
					id: 'gh_4bf03e4d36fb',
					path: '/pages/index/article/article?id=' + this.id + '&server=' + this.userInfo.server_people_id + '&path=/pages/index/article/article',
					webUrl: 'https://uniapp.dcloud.io',
					type: 2
				},
				success: e => {
					console.log('success', e);
					uni.showModal({
						content: '分享成功',
						showCancel: false
					});
					that.$Fetch({
						url: 'news/share',
						data: { news_id: that.id },
						success: res => {
							let str = res > 0 ? '已分享,砖瓦+' + res : '已分享';
							uni.showToast({
								title: str,
								icon: 'none'
							});
						}
					});
				},
				fail: e => {
					console.log('fail', e);
				},
				complete: function() {
					console.log('分享操作结束!');
				}
			});
		},
		//评论
		send() {
			if (!this.content.trim()) {
				uni.showModal({
					content: '请输入内容',
					showCancel: false
				});
				return false;
			}
			this.$Fetch({
				url: 'Comment/add',
				data: {
					comment_type: 0,
					id_value: this.id,
					content: this.content
				},
				success: res => {
					// let comment=this.comment
					let date = new Date();
					let m = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1);
					let d = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
					let time = date.toTimeString().split(' ')[0];
					this.comment.list.unshift({
						user_id: this.userInfo.id,
						user_name: this.userInfo.user_name,
						content: this.content,
						create_time: date.getFullYear() + '-' + m + '-' + d + ' ' + time,
						like_num: 0,
						head_img: this.userInfo.head_img,
						children: null,
						children_num: 0,
						is_like: 0
					});
					this.content = '';
					uni.hideKeyboard();
					this.showInput = false;
				}
			});
		},
		doReply(id, user_name) {
			this.showInput1 = true;
			this.replyId = id;
			this.to_user_name = user_name;
		},
		send1() {
			if (!this.content1.trim()) {
				uni.showModal({
					content: '请输入内容',
					showCancel: false
				});
				return false;
			}
			let parent_id = this.reply.comment.id;
			console.log(parent_id);
			this.$Fetch({
				url: 'Comment/addComment',
				data: {
					parent_id: parent_id,
					to_user_id: this.replyId,
					content: this.content1
				},
				success: res => {
					// let comment=this.comment
					let date = new Date();
					let m = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1);
					let d = date.getDate() > 9 ? date.getDate() : '0' + date.getDate();
					let time = date.toTimeString().split(' ')[0];
					if (this.replyId) {
						this.reply.list.unshift({
							user_id: this.userInfo.id,
							user_name: this.userInfo.user_name,
							content: this.content1,
							create_time: date.getFullYear() + '-' + m + '-' + d + ' ' + time,
							like_num: 0,
							head_img: this.userInfo.head_img,
							is_like: 0,
							to_user_name: this.to_user_name
						});
					} else {
						this.reply.list.unshift({
							user_id: this.userInfo.id,
							user_name: this.userInfo.user_name,
							content: this.content1,
							create_time: date.getFullYear() + '-' + m + '-' + d + ' ' + time,
							like_num: 0,
							head_img: this.userInfo.head_img,
							is_like: 0,
							to_user_name: this.to_user_name
						});
					}
					this.content1 = '';
					uni.hideKeyboard();
					this.showInput1 = false;
				}
			});
		},
		getRecommend() {
			this.$Fetch({
				url: 'news/get_recommend',
				data: { type: this.type },
				success: res => {
					this.list = res;
				}
			});
		},
		//关注
		doAttention(id) {
			this.$Fetch({
				url: 'Attention/add',
				data: { id: id },
				debug: true,
				success: res => {
					this.attention = res.success;
					let str = res.success === 1 ? '已关注,砖瓦+' + res.point : '已取消关注';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//收藏
		doCollect(type) {
			this.$Fetch({
				url: 'Collect/add',
				data: {
					collect_type: type,
					id_value: this.id
				},
				success: res => {
					this.is_collect = res.success;
					let str = res.success === 1 ? '已收藏,砖瓦+' + res.point : '已取消收藏';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//文章点赞
		doZan(type) {
			this.$Fetch({
				url: 'user_like/add',
				data: {
					like_type: type,
					id_value: this.id
				},
				success: res => {
					this.is_like = res.success;
					let str = res.success === 1 ? '已点赞,砖瓦+' + res.point : '已取消点赞';
					uni.showToast({
						title: str,
						icon: 'none'
					});
				}
			});
		},
		//获取评论列表
		getCommentList() {
			let data = this.comment;
			if (data.loading) {
				data.loading = false;
				this.comment = data;
				this.$Fetch({
					url: 'Comment/getData',
					data: {
						comment_type: 0,
						id_value: this.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					success: res => {
						let arr = res.lists;
						if (arr.length > 0) {
							data.list = data.list.concat(arr);
							if (arr.length === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						} else {
							data.more = false;
						}
						this.comment = data;
					}
				});
			}
		},
		//评论点赞
		addCommentLike(item, index) {
			this.$Fetch({
				url: 'Comment/addCommentLike',
				data: { id: item.id },
				success: res => {
					item.is_like = res;
					if (res === 1) {
						item.like_num++;
					} else {
						item.like_num--;
					}
				}
			});
		},
		closePopup() {
			this.show = false;
			this.showInput1 = false;
			this.reply = {
				comment: {},
				pageNum: 1,
				pageSize: 10,
				list: [],
				loading: true,
				more: false
			};
		},
		showPopup(item) {
			this.item = item;
			this.getReply(item);
			this.show = true;
		},
		getReply(item) {
			let data = this.reply;
			data.comment = item;
			this.reply = data;
			if (data.loading) {
				data.loading = false;
				this.reply = data;
				this.$Fetch({
					url: 'Comment/getComment',
					data: {
						id: item.id,
						pageNum: data.pageNum,
						pageSize: data.pageSize
					},
					success: res => {
						let len = res.length;
						if (len > 0) {
							data.list = data.list.concat(res);
							if (len === data.pageSize) {
								data.pageNum++;
								data.loading = true;
								data.more = true;
							} else {
								data.more = false;
							}
						} else {
							data.more = false;
						}
						this.reply = data;
						this.show = true;
					}
				});
			}
		}
	}
};
